.comission_list_container {
    /* border: 2px solid red; */
}

.comission_list_bar {
    /* border: 2px solid rgb(125, 120, 120); */
    display: flex;
    background-color: #005B8F;
    width: 100%;
    
}

.comission_list_page .active {
    color: rgb(0, 0, 0);
    font-weight: 700;
    padding-right: 30px;
}

.list_home_icon {
    /* border: 2px solid brown; */
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 20px;
}

.list_home_icon img {
    height: 40px;
}

.comission_list_page {
    /* border: 3px solid; */
    display: flex;
    width: 100%;
    justify-content: space-between;
    margin-left: 4%;
    /* padding: 0px 50px; */
}

.comission_list_page p {
    color: #FFFFFF;
    font-weight: 600;
    font-size: 15px;
    justify-content: center;
    text-align: center;
    width: 100%;
}

.comission_list {
    padding: 0 10%;
    /* border: 2px solid red; */
}

.comission_percentwise {
    /* border: 2px solid pink; */
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #D9D9D9;
    margin-top: 25px;
    border-radius: 5px;
    padding: 0px 20px;
}

.comission_percentwise img {
    height: 35px;
}

.comission_percentwise p {
    font-size: 18px;
    font-weight: 600;
    text-align: center;
}

.view_more {
    /* border: 2px solid green; */
    display: flex;
    justify-content: center;
    align-items: center;
    /* background-color: #D9D9D9; */
    margin-top: 25px;
    border-radius: 5px;
    margin-bottom: 20px;
}

.view_more img {
    height: 40px;
}

.view_more p {
    font-size: 18px;
    font-weight: 600;
    text-align: center;

}

.cards {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    /* border: 2px solid green; */
}
.card_body{
    background-color: white;
    width: 250px;
    /* height: 200px; */
    padding: 10px;
    border-radius: 10px;
    margin: 10px;    
    box-shadow: inset 0 -3em 3em rgba(0, 0, 0, 0.1), 0 0 0 2px rgb(255, 255, 255),
    0.3em 0.3em 1em rgba(0, 0, 0, 0.3);
}
.title_text{
    color:"black";
    font-size:15px;
    font-weight:bold ;
}
.button_container{
    display: flex;
    width: 100%;
    justify-content: space-evenly;
    align-items: center;
}
.user_card_button{
    background-color: #005B8F;;
    border: 0px;
    border-radius: 2px;
    font-weight: bold;
    padding: 10px;
    font-size: 10px;
    color: white;
}
.title_text_1{
    color:"black";
    font-size: 10px;
    font-weight:bold ;
    padding-left: 10px;
}
.trash_button{
    background-color: white;
    box-shadow: inset 0 -3em 3em rgba(0, 0, 0, 0.1), 0 0 0 2px rgb(255, 255, 255),
    0.3em 0.3em 1em rgba(0, 0, 0, 0.3);
    border: 0px;
    border-radius: 2px;
}
/* Media Queary */
@media screen and (max-width:1290px) {
    .comission_list
    {
        padding: 0 4%;
    }
}
@media screen and (max-width:380px) {
    .comission_list
    {
        padding: 0 1%;
    }
    .comission_percentwise img {
        height: 25px;
    }
}